import { getCurrentUser, updateUser } from '@/api/setting'
export default {
    name: "setting",
    middleware: 'auth',
    data() {
        return {
            userData: {
                image: '',
                username: '',
                email: '',
                bio: '',
                password: null,
            },
            loading: false
        }
    },
    async asyncData() {
      const { data } = await getCurrentUser()
      const { user } = data;
      return {
          user
      }
    },
    mounted() {
        this.userData = {
            image: this.user.image,
            username: this.user.username,
            email: this.user.email,
            bio: this.user.bio
        }
    },
    methods: {
        async updateSetting() {
            this.loading = true
            await updateUser({
                user: this.userData
            })
            this.loading = false
        }
    }
}
